<template>
  <div class="selections">
    <select  id="" @change="clickFirstSelection">
      <option v-for="(item,index) in regionTree" :key="item.id" :value="index">{{item.name}}</option>
    </select>
    <select  id="" @change="clickSecondSelection">
      <option v-for="(item,index) in cityList" :key="item.id" :value="index">{{item.name}}</option>
    </select>
    <select  id="" @change="clickThirdSelection">
      <option v-for="(item,index) in regionList" :key="item.id" :value="index">{{item.name}}</option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'selections-component',
  props: ['regionTree'],
  data () {
    return {
      provinceIndex: 0,
      cityIndex: 0,
      regionIndex: 0
    }
  },
  created () {
  },
  watch: {
    regionTree (newValue) {
      if (newValue) {
        // console.log('updated region tree:', this.regionTree)
      }
    }
  },
  methods: {
    clickFirstSelection (event) {
      // console.log('clicked province:', event.target.value)
      this.provinceIndex = event.target.value
      // console.log('current city list:', Object.values(this.regionTree[this.provinceIndex].city))
    },
    clickSecondSelection (event) {
      this.cityIndex = event.target.value
    },
    clickThirdSelection (event) {
      this.regionIndex = event.target.value
      this.$emit('selectedRegions', this.region)
      // console.log('final:', this.region)
    }
  },
  computed: {
    cityList () {
      // console.log('current city list:', Object.values(this.regionTree[this.provinceIndex]))
      return Object.values(this.regionTree[this.provinceIndex].city)
    },
    regionList () {
      return Object.values(this.cityList[this.cityIndex].region)
    },
    region () {
      return [
        {
          value: this.regionTree[this.provinceIndex].id,
          label: this.regionTree[this.provinceIndex].name
        },
        {
          value: this.cityList[this.cityIndex].id,
          label: this.cityList[this.cityIndex].name
        },
        {
          value: this.regionList[this.regionIndex].id,
          label: this.regionList[this.regionIndex].name
        }
      ]
    }
  }
}
</script>

<style>

select {
  width:30%;
  padding: 8px;
  margin: 0 2px; /* 选项之间的间距 */
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  font-size: 12px;
  box-sizing: border-box;
  text-align: center; /* 文字居中 */
}
</style>
